.header {
  // height: 825px; //!!!
  padding-top: 21px;
  padding-bottom: 238px; // PP

  color: $white;
  background-color: $promo-back;
  background-image: url("../img/promo-bike.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-resolution: 2dppx) {
    padding-top: 0;
    background-image: url("../img/promo-bike@2x.jpg");
  }

  @media (max-width: $tablet-width-max) {
    padding-bottom: 0;
    background-size: contain;
    background-position: center -50px;
  }
}

.header__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  // max-width: 910px;
  max-width: calc(880px + 30px);
  // border: 1px solid green;

  @media (max-width: $tablet-width-max) {
    // display: flex;
    // flex-wrap: wrap;
  }

}

.header__nav {
  margin-bottom: 256px;
  grid-column: 1 / 3;

  @media (max-width: $tablet-width-max) {
    // display: none;
    grid-row: 1 / 2;
    outline: 1px solid red;
    margin-left: -20px;
    margin-right: -20px;
    margin-bottom: 0;

    background-color: $white;
  }
}

.header__logo-link {
  @media (max-width: $tablet-width-max) {
    margin-top: 25px;
    order: -3;
  }
}

.header__toggle {
  display: none;

  @media (max-width: $tablet-width-max) {
    order: -2;
    width: 50px;
    height: 50px;
    display: block;
    margin-left: auto;
    margin-right: -20px;

    background-image: url("../img/icon-menu.svg");
    font-size: 30px;
    background-position: center;
    background-repeat: no-repeat;
    background-color: $aqua;
    border: none;
  }

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.header__slogan {
  grid-column: 1 / 2;
  margin-top: 27px;
  margin-bottom: 23px;
  width: 100%;
  font-size: 46px;
  line-height: 64px;
  font-weight: 700;
  letter-spacing: 0.9px;

  @media (max-width: $tablet-width-max) {
    margin-top: 84px;
    margin-bottom: 21px;
    grid-column: 1 / 3;

    font-size: 30px;
    line-height: 42px;
    letter-spacing: 0.6px;
    text-align: center;
  }
}

.header__text {
  grid-column: 1 / 2;
  width: 90%;
  margin-top: 0;
  margin-bottom: 0;
  color: rgba($white, 0.8);
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: -0.6px;

  @media (max-width: $tablet-width-max) {
    width: 435px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 80px;
    grid-column: 1 / 3;
    font-size: 18px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -0.5px;
  }
}

.header__feedback {
  grid-row: 2 / 5;
  grid-column: 2 / 3;
  margin-left: 30px;
  outline: 1px solid red;

  @media (max-width: $tablet-width-max) {
    order: 1;
    grid-column: 1 / 3;
    grid-row: auto;
  }
}
